@charset "UTF-8";

*{
  margin: 0px; padding: 0px;
}

a{
  text-decoration: none;
  color: #333;
}

.header{
  width: 100%; height: 80px;
  background: rgb(124, 207, 113);
  text-align: center;
}

.header h1{
  line-height: 40px;
}

.footer{
  width: 100%; line-height: 40px;
  background: #333;
  text-align: center;
  color: #fff;
}

.footer address{
  color: #fff; text-align: center;
}

p{
  padding: 10px 0px;
}

.navi ul{display: flex;width: 70%; justify-content: space-evenly; margin:0 auto ;}
.navi li{padding: 0px 5px; list-style: none;}

.movie_list{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 90%; margin: 0 auto
  ;
}
.movie_list ul{
  width: 300px;
}
.movie_list li{
  list-style: none;
}